/* The container <div> - needed to position the dropdown content */
.dropdown {

    position: relative;
    /* margin-top: 24px; */
    display: inline-block;
   
 
}





/* Dropdown Content (Hidden by Default) */
.dropdown-content {
    cursor: pointer;
    display: none;
    color: #000;
    position: absolute;
    background-color: #a7a0a0;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}


.dropdown-contents {
   
    display: none;
    /* color: rgb(221, 12, 12); */
    position: absolute;
    background-color:#201f1f;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}





